.skillsBlock {
    min-height: 80vh;
    background-color: rgba(176, 182, 119, 0.7);
    display: flex;
    align-items: center;
}

.container {
    border: 1px solid;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.title {
background-color: darkgrey;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 5px
}