.mainBlock {
    height: 100vh;
    background-color: rgba(182, 145, 119, 0.78);
}

.invitation {
    width: 300px;
    background-color: rgba(248, 248, 255, 0.45);
    box-sizing: border-box;
    padding: 20px;

}

.photoContainer {
    width: 300px;
    height: 400px;
    background-color: rgba(248, 248, 255, 0.45);

}

.container {
    border: 1px solid;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}