.projectsBlock {
    min-height: 100vh;
    background-color: rgba(205, 205, 255, 0.84);
    display: flex;
    align-items: center;
}

.container {
    border: 1px solid;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.title {
    background-color: darkgrey;
}

.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 10px
}