.footerBlock{
    min-height: 40vh;
    background-color: rgba(127, 255, 0, 0.5);
    display: flex;
    align-items: flex-end;
}

.container {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px auto;
    border: 1px solid;


}

.title{
    background-color: gray;
}

.iconsContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 280px;
    border: 1px solid;

}

.icon {
    height: 50px;
    width: 50px;
    background-color: gray;
}

.rights {
    background-color: gray;
}