.project {
    width: 350px;
    background-color: rgba(128, 128, 128, 0.45);
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    padding: 5px;
}

.imageContainer {
    height: 300px;
    width: 100%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projectName {
    background-color: gray;
}

.description {
    text-align: center;
    background-color: gray;
}

.more {
    font-size: 20px;
    width:30%;
    height: 30px;
    background-color: gray;
    text-align: center;
}