.contactsBlock {
    min-height: 80vh;
    background-color: rgba(255, 215, 0, 0.42);
    display: flex;
    align-items: center;
}

.container {
    border: 1px solid;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.title {
    background-color: gray;
}

.form {
    display: flex;
    flex-direction: column;
    border: 1px solid;
    width: 270px;
    gap:10px;
}

.submitInput {
    width: 100px;
    height: 30px;
    margin-top: 20px;
}

.textarea {
    min-height: 100px;
}